import axios from "axios";
import React, { Component } from "react";
import { AcademicCalendar } from "../../actions";
import { APP_URL } from "../../config/app.config";
import DashboardContainer from "../DashboardContainer/DashboardContainer";
import Loading from "../Loading/Loading";

interface TransferAlgorithmResultDataInterface {
  vacant_posts: string;
  district_posts: string;
  placed_teachers: string;
  district_id: string;
  school_info: {
    school_name: string;
    school_code: string;
    sector_name: string;
    district_name: string;
  } | null;
  position_info: {
    qualification_id: string;
    position_code: string;
    position_name: string;
  };
}

interface TransferAlgorithmResultInterface {
  transfer_results_logs_id: string;
  round_id: string;
  academic_id: string;
  datas: TransferAlgorithmResultDataInterface[];
  status: string;
}

interface TransferAlgorithmResultsProps {
  academic_calendar: AcademicCalendar;
  reload: () => void;
  close: () => void;
}
interface TransferAlgorithmResultsState {
  loading: boolean;
  data: TransferAlgorithmResultInterface[] | null;
  round: string;
}

export class TransferAlgorithmResults extends Component<
  TransferAlgorithmResultsProps,
  TransferAlgorithmResultsState
> {
  constructor(props: TransferAlgorithmResultsProps) {
    super(props);

    this.state = {
      loading: false,
      data: null,
      round: "1",
    };
  }
  GetAlgorithmResults = async (academic_id: string) => {
    this.setState({ loading: true });
    try {
      const res = await axios.get<TransferAlgorithmResultInterface[]>(
        `${APP_URL}/teachertransfer/results/${academic_id}`
      );
      this.setState({
        data: res.data,
        loading: false,
        round:
          res.data.length === 0 ? "1" : res.data[res.data.length - 1].round_id,
      });
      console.log("Res: ", res.data);
    } catch (error: any) {
      this.setState({ data: [], loading: false });
      console.log("error: ", error);
      console.log("error: ", { ...error });
    }
  };
  getData = (): TransferAlgorithmResultDataInterface[] => {
    if (this.state.data !== null) {
      const test = this.state.data.find(
        (item) => item.round_id === this.state.round
      );
      if (test !== undefined) {
        return test.datas;
      }
    }
    return [];
  };
  getRounds = () => {
    let rounds: string[] = [];
    if (this.state.data !== null) {
      for (let item of this.state.data) {
        rounds = [...rounds, item.round_id];
      }
    }
    return rounds;
  };
  componentDidMount = () => {
    if (this.state.data === null) {
      this.GetAlgorithmResults(this.props.academic_calendar.academic_year_id);
    }
  };
  render() {
    if (this.state.loading === true || this.state.data === null) {
      return (
        <DashboardContainer>
          <div className="flex flex-col items-center justify-center gap-3 w-full text-center py-5">
            <Loading />
            <div>Loading...</div>
          </div>
        </DashboardContainer>
      );
    }
    return (
      <div className="p-4">
        <div>
          <div className="text-center">
            <span className="font-bold text-3xl text-green-700">
              Transfer algorithm results
            </span>
            <div className="flex items-center justify-center my-3 gap-3">
              <select
                // value={this.state.round}
                onChange={(e) => this.setState({ round: e.target.value })}
                className="border border-gray-500 px-3 py-2 rounded"
              >
                {this.getRounds().map((item, i) => (
                  <option key={i + 1} value={item}>
                    Round {item}
                  </option>
                ))}
              </select>
              <div>
                <div
                  onClick={() =>
                    this.GetAlgorithmResults(
                      this.props.academic_calendar.academic_year_id
                    )
                  }
                  className="bg-accent-900 text-white px-3 py-2 rounded cursor-pointer"
                >
                  Reload results
                </div>
              </div>
              <div>
                <div
                  onClick={this.props.close}
                  className="bg-gray-200 hover:bg-gray-300 px-3 py-2 rounded cursor-pointer"
                >
                  Close window
                </div>
              </div>
            </div>
          </div>
        </div>
        <div>
          {this.state.data.length === 0 ? (
            <div className="bg-gray-200 rounded-md p-6 text-center text-3xl">
              No result found!
            </div>
          ) : (
            <table className="w-full">
              <thead className="bg-blue-100">
                <tr>
                  <th className="px-3 py-2 text-left">#</th>
                  <th className="px-3 py-2 text-left">District</th>
                  <th className="px-3 py-2 text-left">School code</th>
                  <th className="px-3 py-2 text-left">School name</th>
                  <th className="px-3 py-2 text-left">Position</th>
                  <th className="px-3 py-2 text-left">Teachers placed</th>
                  <th className="px-3 py-2 text-left">Vacants</th>
                </tr>
              </thead>
              <tbody>
                {this.getData().map((item, i) => (
                  <tr key={i + 1} className="border-b">
                    <td className="px-3 py-2 text-left">{i + 1}</td>
                    <td className="px-3 py-2 text-left">
                      {item.school_info?.district_name}
                    </td>
                    <td className="px-3 py-2 text-left">
                      {item.school_info?.school_code}
                    </td>
                    <td className="px-3 py-2 text-left">
                      {item.school_info?.school_name}
                    </td>
                    <td className="px-3 py-2 text-left">
                      {item.position_info.position_name}
                    </td>
                    <td className="px-3 py-2 text-left">
                      {item.placed_teachers}
                    </td>
                    <td className="px-3 py-2 text-left">{item.vacant_posts}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      </div>
    );
  }
}
